<template>
  <div>
    <q-card v-for="course in courses" class="gradeCard" :key="course.name">
      <q-card-section>

      </q-card-section>
    </q-card>
    <q-dialog
        v-model="dialog"
      persistent
      :maximized="true"
      transition-show="slide-up"
      transition-hide="slide-down"
      >
    </q-dialog>
  </div>
</template>

<script>
import PowerSchool from "powerschool";

const district_Code = "JPJK";

// init a powerschool object with a district
// code
var ps = new PowerSchool(district_Code);

// ready is fired when the district's server info
// has been retrieved
ps.on("ready", district => {
  ps.getStudentData("kcrooks", "20010442").then(data => {
    console.log(district, data);
  }).catch(console.error);
});


export default {
  data(){
    return {
      courses: [],
      dialog: true,
    }
  },
  mounted(){
  }
};
</script>

<style lang="scss" scoped>
.gradeCard {
  width: 90%;
  margin: 0px auto;
}
</style>